* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}
.timer-container {
  max-width: 600px;
}

body > input {
  font-family: 'Trebuchet MS', sans-serif;
  padding: 25px 45px;
  margin-top: 20px;
  outline: none;
  background-color: transparent;
  font-size: 25px;
  line-height: 1.9;
  color: #4b3e3e;
}

button[data-start] {
  margin-left: 30px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.87;
  text-align: center;
  color: #fff;
  background-color: rgba(102, 219, 66, 0.726);
  cursor: pointer;
  border: none;
  border-radius: 2px;
  padding: 16px 35px;
  align-items: center;
}

button:disabled {
  background-color: rgb(125, 206, 179);
  border: none;
  cursor: not-allowed;
  box-shadow: none;
}

.timer {
  margin-top: 25px;
  display: flex;
}
.field {
  display: flex;
  flex-wrap: wrap;
}

.label {
  font-family: 'Trebuchet MS', sans-serif;
  display: block;
  width: 100%;
  text-align: center;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.2;
  text-align: center;
  color: #5f4b4b;
}
.value {
  font-family: 'Times New Roman', serif;
  display: block;
  width: 100%;
  text-align: center;
  font-weight: 700;
  font-size: 60px;
  line-height: 1.2;
  text-align: center;
  color: #5f4b4b;
}
